export default {
	data() {
		return {
			loginTitle: '四六级查询',
			stuNum: '',
			password: '',
			btnText: '查询'
		}
	},
	methods: {
		inputNum(e){
			this.stuNum = e.detail.value;
			console.log(e);
		},
		inputPasswd(e){
			this.password = e.detail.value;
		},
		login(){
			if(this.stuNum || this.password){
				uni.showModal({
					content: '是否需要将图书馆密码保存到手机中，方便下次查询',
					cancelText: '不保存',
					confirmText: '保存'
				});
				uni.navigateTo({
					url: '../cetScore.vue/cetScore.vue'
				})
			}else{
				uni.showModal({
					title: '信息不全',
					content: '请填写完整信息',
					showCancel: true
				})
			}
			this.stuNum = this.password = '';
		}
	}
}
